.header {
    background-color: #1b1b1b;
    height: 100px;
    display: flex;
    width: 100%;
}

.header-wrapper {
    background-color: transparent;
    display: flex;
    align-self: center;
    width: inherit;
    height: inherit;
}

.header-wrapper-inner {
    width: inherit;
    padding: 0 12rem;
    margin: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-wrapper-inner-logo {
    color: white;
    font-size: 1.5rem;
}