.postCard {
    background-color: transparent;
    border-radius: 15px;
}

.postCard-wrapper-header {
    margin-bottom: 30px;
}

.postCard-wrapper-header-title {
    color: white;
    font-size: 30px;
}

.postCard-wrapper-header-date {
    color: lightgray;
    font-size: 15px;
}

.postCard-wrapper-content img {
    width: 100%; /* this makes the image width equal to that of the container */
    height: auto; /* maintains the aspect ratio of the image */
    display: block; /* removes any extra space below the image */
}