.app {
    background-color: transparent;
    height: 100%;
    width: 100%;
}

.app-wrapper {
    width: inherit;
    height: calc(100% - 100px);
    display: grid;
    background-color: transparent;
    overflow-y: auto;
}

.app-wrapper-inner {
    padding: 0 12rem;
    background-color: transparent;
    display: grid;
    grid-template-columns: auto 300px;
    position: relative;
    gap: 2rem;
}

.app-wrapper-inner-right {
  background-color: #3a3a3a;
  position: fixed; /* Keeps the div fixed during scroll */
  right: 12rem; /* Adjust this value to align with your grid layout */
  top: calc(100px + 2rem); /* Adjust if you have a header and you want the div below it */
  bottom: 0; /* This stretches the div to the bottom of the view */
  width: calc(300px);
  border-radius: 15px;
  text-align: center;

}



/* You may need to add some right padding to the left div to prevent it from being hidden behind the fixed right div */
.app-wrapper-inner-left {
  background-color: transparent;
  padding: 2rem 0;
}

